section {
  z-index: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

video {
  z-index: -1;
  object-fit: fill;
  width: 100%;
  min-width: 100%;
  height: auto;
  min-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content-overlay {
  text-align: left;
  color: #eff0f6;
  z-index: 2;
  backdrop-filter: blur(30px);
  background: #0b0e1405;
  width: 50%;
  padding: 2rem;
  position: absolute;
  top: 50%;
  left: 45%;
  transform: translate(-80%, -50%);
}

.content-overlay a {
  text-transform: uppercase;
  color: #bde25a;
  text-decoration: none;
}

.content-overlay h1 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 3rem;
}

button {
  z-index: 1;
  background-color: #0000;
  border: 1px solid #bde25a;
  width: 150px;
  height: 50px;
  transition: all .5s ease-in;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

button:before, button:after {
  content: "";
  z-index: -1;
  width: 0;
  height: 100%;
  transition: all .5s;
  position: absolute;
  top: 0;
  overflow: hidden;
  transform: skew(15deg);
}

button:before {
  background: #0a13c2;
  left: -10px;
}

button:after {
  background: #0a13c2;
  right: -10px;
}

button:hover:before, button:hover:after {
  width: 58%;
}

button:hover span {
  color: #eff0f6;
  transition: all .3s;
}

button span {
  color: #eff0f6;
  font-size: 18px;
  transition: all .3s ease-in;
}

.usp-sec {
